*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-size: 10px;
  font-family: sans-serif;
}

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.main-nav {
  width: 100%;
  max-width: 460px;
  padding: 0 24px;
  margin: 0 auto;
  font-size: 0;
  text-align: center;
  transition: top .3s ease;
}

.lib-name {
  display: inline-block;
  padding: 0 0 40px;
  margin: 0;
  font-size: 3.6rem;
  line-height: 1;
  font-family: "Carter One";
  color: #4D3333;
}

.lib-name span {
  display: block;
  text-align: left;
  font-size: .5em;
  font-weight: 400;
}

.main-nav__link {
  display: inline-block;
  width: 100%;
  padding: 30px 0;
  font-size: 5vw;
  line-height: 1;
  text-decoration: none;
  color: #fff;
  background-color: #F67A37;
  border-bottom: 5px solid #F55600;
  transition: background .3s ease;
}

@media screen and (min-width: 450px) {
  .main-nav__link {
    font-size: 1.6rem;
  }
}

@media screen and (min-width: 768px) {
  .main-nav__link {
    width: 50%;
  }
}

.main-nav__link:hover {
  background-color: #F55600;
}

.content {
  width: 100%;
  max-width: 64em;
  padding: 0 24px;
  margin: 80vh auto;
  font-size: 1rem;
  font-family: "Fondamento";
}

.content--scrollable {
  height: 400px;
  margin: 0 auto;
  margin-top: 80px;
  overflow-y: scroll;
}

.content p:first-child:first-letter {
  font-size: 6em;
}

.content__p {
  font-size: 1.6em;
  line-height: 200%;
}

@media screen and (min-width: 450px) {
  .content__p {
    font-size: 2em;
  }
  .content--scrollable {
    height: 500px;
  }
}

.back-btn {
  display: block;
  position: fixed;
  left: 24px;
  bottom: 20px;
  width: 0;
  height: 0;
  text-indent: -10000px;
  border-top: 24px solid transparent;
  border-bottom: 24px solid transparent;
  border-right: 24px solid #F55600;
}

.bouncing-arrow {
  display: block;
  position: relative;
  top: 50vh;
  width: 0;
  height: 0;
  margin: 0 auto;
  border-left: 36px solid transparent;
  border-right: 36px solid transparent;
  border-top: 36px solid #4D3333;
  animation: .5s linear 0s infinite alternate bouncing_arrow;
}

.bouncing-arrow--upwards {
  top: unset;
  border-top: none;
  bottom: 50vh;
  border-bottom: 36px solid #4D3333;
}

@keyframes bouncing_arrow {
  0% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-16px);
  }
  60% {
    transform: translateY(-18px);
  }
  80% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(-21px);
  }
}
